import React from "react"

const Lightbox = (props) => {
    

        if(props.lightboxStatus === true){
            return(
                <div className="lightbox" onClick={props.handleLightbox}>
                    <img src={props.lightboxGif} className="lightboxGif" alt="#"></img>    
                </div>
            )
        }
        else return null
    
}

export default Lightbox





