import React, { Component } from "react"
import Input from "./Components/Input"
import GifPanel from "./Components/GifPanel"
import Lightbox from "./Components/Lightbox"
import Modal from "react-modal"

export default class App extends Component {
    constructor(props){
        super(props)
        this.state = {
            input: null,
            gifURL: [],
            lightboxStatus: false,
            lightboxGif: null,
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.input !== prevState.input) {
            var gif_search = this.state.input
            var api_key = "&api_key=OP1upGnWxQDoVvIXWWAMd8TpK4m4oSl2"
            var limit = 27
            this.setState({
                gifURL: [],
            })
            fetch("//api.giphy.com/v1/gifs/search?q=" + gif_search.trim().replace(/ /g, "+") + api_key + "&limit=" + limit)
                .then((res) => {

                    res.json().then((res) => {

                        for (let i = 0; i < limit; i++) {
                            this.setState({
                                gifURL: [...this.state.gifURL, res.data[i].images.original.url]
                            })
                        }
                        console.log(this.state.gifURL)
                    })
                })
        }
    }


    handleSubmit = (e) => {
        this.setState({
            input: e
        }, () => {
            console.log(this.state.input)
        })
    }

    handleLightbox = (e) => {
        this.setState({
            lightboxStatus: !this.state.lightboxStatus,
            lightboxGif: e.target.src
        })
    }
    
    render(){
        return(
            <div>
            <Input onSubmit={this.handleSubmit}/>
            <div className="grid-container">
                {this.state.gifURL.map((i, index) => <GifPanel 
                                                        gifURL={i} 
                                                        key={index} 
                                                        handleLightbox={this.handleLightbox}/>)}
            </div>
            <Lightbox lightboxStatus={this.state.lightboxStatus} 
                lightboxGif={this.state.lightboxGif} 
                handleLightbox={this.handleLightbox}
                shouldCloseOnOverlayClick={true}/>
            </div>
        )
    }
}


