import React from "react"

const GifPanel = (props) => {

        return(
                <div className="gif-box">
                    <img 
                        className="gifs" 
                        src={props.gifURL} 
                        alt="#" 
                        onClick={props.handleLightbox}>
                    </img>
                </div>
        )

}

export default GifPanel


